.show-case {
  display: flex;
  flex-direction: column;
  width: 400px;
  height: 300px;
  padding: 16px;
  border-radius: 8px;
  background-color: var(--s-color-bg-dark-2);
}

.title {
  overflow: hidden;
  color: var(--s-color-text-light-1);
  font-weight: bold;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.tags {
  overflow: hidden;
  color: var(--s-color-text-light-2);
  font-size: var(--s-font-size-small);
  text-overflow: ellipsis;
  white-space: nowrap;
}

.frame__wrapper {
  position: relative;
  flex: 1;
  overflow: hidden;
  margin-bottom: 8px;
  border-radius: 8px;

  &:hover {
    cursor: pointer;
  }

  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    display: none;
    width: 200px;
    height: 150px;
    background: linear-gradient(to top right, transparent 50%, rgb(0 0 0 / 50%) 100%);
  }
}

.frame__wrapper:hover::after {
  display: block;
}

.frame {
  height: 100%;
  background: var(--s-color-bg-light-1);
}

.frame__btn {
  position: absolute;
  top: 20px;
  right: 20px;
  display: none;
  width: 28px;
  height: 28px;
  border-radius: 6px;
  background: var(--s-color-bg-dark-5);
  line-height: 28px;
  text-align: center;
  z-index: 10;
  font-size: 24px;

  &:hover {
    transform: scale(1.2);
  }
}

.frame__wrapper:hover .frame__btn {
  display: block;
}
